@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.coin-container{
    display: flex;
    justify-content: center;
}

.coin-row{
    display: flex;
    flex-direction: row;
    justify-items: start;
    align-items: center;
    height: 80px;
    border-bottom: 1px solid #d7d7d7;
    width: 900px;
}

.coin{
    display: flex;
    align-items: center;
    padding-right: 24px;
    min-width: 300px;
}

.coin h1{
    font-size: 16px;
    width: 150px;
}

.coin img{
    height: 30px;
    width: 30px;
    margin-right: 10px;
}

.coin-symbol{
    text-transform: uppercase;
}

.coin-data{
    display: flex;
    text-align: center;
    justify-content: space-between;
    width: 100%;
}

.coin-price{
    width: 110px;
}

.coin-volume{
    width: 155px;
}

.coin-marketcap{
    width: 250px;
}

.coin-percent{
    width: 90px;
}

.red{
    color: #f00606;
}

.green{
    color: #11d811;
}

@media only screen and (max-width: 600px) {
    .coin-container{
        display: flex;
        justify-content: center;
    }
    
    .coin-row{
        display: flex;
        flex-direction: row;
        justify-items: center;
        align-items: center;
        height: 80px;
        border-bottom: 1px solid #d7d7d7;
        width: 300px;
    }
    
    .coin{
        display: flex;
        align-items: center;
        padding-right: 14px;
        min-width: 200px;
    }
    
    .coin h1{
        font-size: 10px;
        width: 80px;
    }
    
    .coin img{
        height: 25px;
        width: 25px;
        margin-right: 25px;
    }
    
    .coin-symbol{
        text-transform: uppercase;
        font-size: 10px;
        margin-left: auto;
        margin-right: auto;
    }
    
    .coin-data{
        display: flex;
        text-align: center;
        justify-content: space-between;
        width: 95px;
    }
    
    .coin-price{
        width: 95px;
        font-size: 16px;
    }
    
    .coin-volume{
        display: none;
    }
    
    .coin-marketcap{
       display: none;
    }
    
    .coin-percent{
        display: none;
    }
  }
*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Segoe UI', Tahoma, Verdana, sans-serif;
  font-weight: bold;
  background-color: #1a1a1c;
  color: #fff;
}

.coin-app{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 40px;
  color: #fff;
}

.header{
  width: 100%;
  height: 50px;
  background-image: linear-gradient(-225deg, #ac32e4 0%, #7918f2 48%, #4801ff 100%);
  
}
.header h1{
  background-image: inherit;
  text-align: center;
  padding: 10px;
}

.coin-search{
  margin-bottom: 64px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.coin-text{
  margin-bottom: 32px;
  text-align: center;
}

.coin-input{
  padding-left: 16px;
  width: 300px;
  height: 50px;
  border-radius: 4px;
  border: none;
  background-image: linear-gradient(-225deg, #ac32e4 0%, #7918f2 48%, #4801ff 100%);
  color: #e2e2e2;
}

.coin-input::-webkit-input-placeholder{
  color: #e2e2e2;
}

.coin-input:-ms-input-placeholder{
  color: #e2e2e2;
}

.coin-input::placeholder{
  color: #e2e2e2;
}

.lds-hourglass {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.lds-hourglass:after {
  content: " ";
  display: block;
  border-radius: 50%;
  width: 0;
  height: 0;
  margin: 8px;
  box-sizing: border-box;
  border: 32px solid #fff;
  border-color: #fff transparent #fff transparent;
  animation: lds-hourglass 1.2s infinite;
}
@keyframes lds-hourglass {
  0% {
    transform: rotate(0);
    animation-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
  }
  50% {
    transform: rotate(900deg);
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  }
  100% {
    transform: rotate(1800deg);
  }
}


